@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

:root {
    /* Colores */
    --color-primary: #3498db;
    --color-secondary: #e74c3c;
    --color-accent: #f39c12;
    --color-footer: #999;
    --color-item-text: #6c6c6c;
    --color-hover-text: 4b4b4b;
    --color-nav: #6f6f6f;
    --color-background: #fcfcfc;
    --color-bg-footer: #eaeaea;

    /* Tipograf√≠a */
    --font-family-p: 'Montserrat', sans-serif;
    --font-family-header: 'Poppins', sans-serif;
    --font-size-xs: 12px;
    --font-size-sm: 14px;
    --font-size-base: 16px;
    --font-size-lg: 18px;
    --font-size-xl: 24px;
    --font-size-h1: 82px;
    --font-size-h2: 56px;
    --font-size-h3: 48px;
    --font-size-h4: 34px;
    --font-weight-normal: 400;
    --font-weight-bold: 700;

    /* Espaciado */
    --spacing-unit: 8px;
    --spacing-xs: calc(0.25 * var(--spacing-unit));
    --spacing-sm: calc(0.5 * var(--spacing-unit));
    --spacing-md: calc(1.5 * var(--spacing-unit));
    --spacing-lg: calc(2.5 * var(--spacing-unit));
    --spacing-xl: calc(4 * var(--spacing-unit));

    /* Dimensiones */
    --header-height: 60px;
    --footer-height: 60px;
    --logo-height: 25px;

    /* Bordes y sombras */
    --border-radius: 4px;
    --box-shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
    --box-shadow-md: 0 4px 6px rgba(0, 0, 0, 0.1);
    --box-shadow-lg: 0 10px 15px rgba(0, 0, 0, 0.1);
    --box-shadow-xl: 0 20px 25px rgba(0, 0, 0, 0.1);

    /* Animaciones */
    --transition-duration: 0.3s;

    /* Breakpoints */
    --breakpoint-xs: 480px;
    --breakpoint-sm: 768px;
    --breakpoint-md: 1024px;
    --breakpoint-lg: 1200px;
    --breakpoint-xl: 1600px;
}