section {
    display: flex;
    justify-content: center;
    align-items: center;
}

section .section-home {
    display: flex;
    height: calc(100vh - var(--header-height));
    max-width: var(--breakpoint-lg);
    width: 100%;
    padding: 0 var(--spacing-lg);
    justify-content: center;
    align-items: center;
}

section .section-home .home-description {
    width: 50%;
}

section .section-home .home-description .header-title {
    font-size: var(--font-size-h1);
    font-family: var(--font-family-header);
    margin: 0;
}

section .section-home .home-description .header-subtitle {
    font-size: var(--font-size-h3);
    font-family: var(--font-family-header);
    font-weight: 600;
    line-height: 30px;
    margin-top: 0;
    margin-bottom: 60px;
}

section .section-home .home-description .header-name {
    font-size: var(--font-size-h4);
    font-family: var(--font-family-p);
    font-weight: 600;
    margin: 0;
}

section .section-home .home-description .header-description {
    font-size: var(--font-size-base);
    font-family: var(--font-family-p);
}

section .section-home .home-photo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}

section .section-home .home-photo .my-photo {
    width: 70%;
}